	module.exports = {"data":{"markdownRemark":{"html":"<h1>Overflow</h1>\n<p>The <code>overflow</code> property specifies what happens if content overflows an element’s box (this property only works for block elements with a specified height).</p>\n<p>This property specifies whether to clip content or to add scrollbars when an element’s content is too big to fit in a specified area.</p>\n<p>For example, a given block-level element (<code>&#x3C;div></code>) set to 300px wide, that contains an image that is 400px wide. The image will stick out of the div and be visible by default. However, if the overflow value is set to hidden, the image will cut off at 300px.</p>\n<h2>Values</h2>\n<ul>\n<li><code>visible</code>: This is the default value of the property. Content is not clipped when it’s bigger than the box.</li>\n<li><code>hidden</code>: Overflowing content will be hidden.</li>\n<li><code>scroll</code>: Similar to hidden, but users will be able to scroll through the hidden content.</li>\n<li><code>auto</code>: If the content proceeds outside its box, then that content will be hidden, whilst a scrollbar should be visible for users to read the rest of the content.</li>\n<li><code>initial</code>: Uses the default value which is visible.</li>\n<li><code>inherit</code>: Sets the overflow to the value of its parent element.</li>\n</ul>\n<h2>Examples</h2>\n<h3>Visible:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.box-element</span> <span class=\"token punctuation\">{</span> <span class=\"token property\">overflow</span><span class=\"token punctuation\">:</span> visible<span class=\"token punctuation\">;</span> <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><img src=\"https://s26.postimg.org/gweu6g5yh/1-vissible.png\" alt=\"Example Image\"></p>\n<h3>Hidden:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.box-element</span> <span class=\"token punctuation\">{</span> <span class=\"token property\">overflow</span><span class=\"token punctuation\">:</span> hidden<span class=\"token punctuation\">;</span> <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><img src=\"https://s26.postimg.org/l49mf77e1/2-hidden.png\" alt=\"Example Image\"></p>\n<h3>Scroll:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.box-element</span> <span class=\"token punctuation\">{</span> <span class=\"token property\">overflow</span><span class=\"token punctuation\">:</span> scroll<span class=\"token punctuation\">;</span> <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><img src=\"https://s26.postimg.org/d8z30dxrd/3-scroll.png\" alt=\"Example Image\"></p>\n<h3>Auto:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.box-element</span> <span class=\"token punctuation\">{</span> <span class=\"token property\">overflow</span><span class=\"token punctuation\">:</span> auto<span class=\"token punctuation\">;</span> <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p><img src=\"https://s26.postimg.org/z5q7ei0bt/4-autoank.png\" alt=\"Example Image\"></p>\n<h2>overflow-x and overflow-y</h2>\n<ul>\n<li><code>overflow-x</code>: Allows the user to scroll through the content that extends beyond the height of the box element.</li>\n<li><code>overflow-y</code>: Allows the user to scroll through the content that extends beyond the width of the box.</li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>  <span class=\"token selector\">.box-element</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">overflow-x</span><span class=\"token punctuation\">:</span> scroll<span class=\"token punctuation\">;</span>\n    <span class=\"token property\">overflow-y</span><span class=\"token punctuation\">:</span> auto<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>And the <code>.box-element</code> will look like this:\n<img src=\"https://s26.postimg.org/ff2kmdfzd/5-_Xand_Y.png\" alt=\"Example Image\"></p>\n<p>If the content overflows the Y-axis, then that content will be hidden, whilst a scrollbar should be visible for users to read the rest of the content.</p>","fields":{"slug":"/css/overflow/"},"frontmatter":{"title":"Overflow"}}},"pathContext":{"slug":"/css/overflow/"}}

